import { Divider, Button, Row, Col, Icon, Modal, Carousel } from 'antd'

import { tiles, experiences, skills } from '../components/MyContentData'

const tile = tiles.robot

export default () => (
            <Modal
                title={tile.title}
                centered
                visible={true}
                onOk={() => setOpen(true)}
                onCancel={() => setOpen(false)}
                zIndex={99999}
                mask={true}
                closable={false}
                footer={null}
            >
                <Row type="flex" justify="space-around" align='middle'>
                    <Col span={12}>
                        {tile.paragraphs.map((paragraph, key) => <p key={key}>{paragraph}</p>)}
                        {tile.bullets && 
                            <ul>
                                {tile.bullets.map((bullet, key) => <li key={key}>{bullet}</li>)}
                            </ul>
                        }
                    </Col>
                    <Col span={12}>
                        <Carousel>
                            {tile.slides.map((slide, key) => 
                                <div key={key} 
                                // style={{ backgroundImage: `url(static/images/${slide.source})` }}
                                >
                                    {(slide.source.includes('jpg') || slide.source.includes('jpeg'))
                                        ? <div className='slide-image' style={{ backgroundImage: `url(static/images/${slide.source})` }} />
                                        : <iframe 
                                                // width={100} 
                                                height={400}
                                                src={slide.source} 
                                                frameBorder="0" 
                                                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" 
                                                allowFullScreen
                                                title={slide.source}
                                                crossOrigin='true'
                                            />
                                    }
                                    <p>{slide.description}</p>
                                </div>
                            )}
                        </Carousel>
                    </Col>
                </Row>
            </Modal>
)